import topNavigationBar from "@/components/top-navigation-bar/top-navigation-bar.vue"
export default {
	components: {topNavigationBar},
	data() {
		return {
			current: 0,
			imgUrl: this.$imgUrl,
			info: []
		}
	},
	onLoad:function(option){
		var that = this
		uni.getStorage({
		    key: 'userInfo',
		    success: function (res) {
					that._getBanner()
		    },
			fail:function(res){
				uni.redirectTo({
					url: "../dealerLogin/dealerLogin"
				})
			}
		});
	},
	methods: {
		change (e) {
			this.current = e.detail.current;
		},
		// 获取banner图
		_getBanner () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$http.post('System/get_banner').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.info = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 路径跳转教育学院
		skipCollegeOfEducation () {
			var price = "这里是通往教育学院"
			uni.navigateTo({
				url: '../../pagesDealer/collegeOfEducation/collegeOfEducation?collegeOfEducation=' + price 
			})
		},
		// 路径跳转案例专享
		skipCaseExclusive () {
			var price = "这里是通往案例分享"
			uni.navigateTo({
				url: '../../pagesDealer/caseExclusive/caseExclusive?caseExclusive=' + price
			})
		},
		// 路径跳转最新消息
		skipSpotNews () {
			var price = "这里是通往最新消息"
			uni.navigateTo({
				url: '../../pagesDealer/spotNews/spotNews?spotNews=' + price
			})
		},
		// 路径跳转资讯反馈
		skipConsultingFeedback () {
			var price = "这里是通往资讯反馈"
			uni.navigateTo({
				url: '../../pagesDealer/consultingFeedback/consultingFeedback?consultingFeedback=' + price
			})
		},
		// 路径跳转知识测试
		skipKnowledgeTest () {
			var price = "这里是知识测试"
			uni.navigateTo({
				url: '../../pagesDealer/knowledgeTest/knowledgeTest?knowledgeTest=' + price
			})
		}
	}
}
